import {graphql, PageProps, useStaticQuery} from "gatsby"
import {IGatsbyImageData} from "gatsby-plugin-image"
import {Fragment, useContext} from "react"
import Blank from "~/components/Sections/Blank/Blank"
import HeroSection from "~/components/Sections/HeroSection/HeroSection"
import RichTextField from "~/components/UI/RichText/RichText"
import SectionContainer from "~/components/UI/SectionContainer/SectionContainer"
import I18nContext from "~/contexts/I18nContext"

interface NotFoundPageProps extends PageProps {}

const NotFoundPage: React.FC<NotFoundPageProps> = props => {
    const i18n = useContext(I18nContext)

    const data: Queries.NotFoundPageQuery = useStaticQuery(graphql`
        query NotFoundPage {
            allContentfulPage404 {
                edges {
                    node {
                        heroImage {
                            title
                            url
                            gatsbyImageData(
                                layout: FULL_WIDTH
                                formats: [AUTO, AVIF, WEBP]
                                placeholder: DOMINANT_COLOR
                                quality: 90
                                breakpoints: [1920]
                            )
                        }
                        heroImageMobile {
                            url
                            gatsbyImageData(
                                layout: FULL_WIDTH
                                formats: [AUTO, AVIF, WEBP]
                                placeholder: DOMINANT_COLOR
                                quality: 90
                                breakpoints: [1920]
                            )
                            title
                        }
                        title
                        description {
                            raw
                        }
                        node_locale
                    }
                }
            }
        }
    `)

    let notFoundPageData = data?.allContentfulPage404?.edges?.[0]?.node

    for (let footerTranslation of data.allContentfulPage404.edges) {
        if (footerTranslation.node.node_locale === i18n.language) {
            notFoundPageData = footerTranslation.node
            break
        }
    }

    return (
        <Fragment>
            <HeroSection
                image={notFoundPageData?.heroImage?.gatsbyImageData as IGatsbyImageData | null | undefined}
                imageAlt={notFoundPageData?.heroImage?.title}
                mobileImage={notFoundPageData?.heroImageMobile?.gatsbyImageData as IGatsbyImageData | null | undefined}
            >
                <h1 className="hero-title-part-2-d2bh">{notFoundPageData?.title}</h1>
            </HeroSection>
            <SectionContainer>
                <Blank>
                    <h6>
                        <RichTextField>{notFoundPageData?.description?.raw}</RichTextField>
                    </h6>
                </Blank>
            </SectionContainer>
        </Fragment>
    )
}

export default NotFoundPage
